
@mixin box {
	box-sizing: border-box; }
@mixin shadow-border($width, $color) {
	-webkit-box-sizing: inset 0px 0px 0px $width $color {
		-moz-box-sizing: inset 0px 0px 0px $width $color {
			box-shadow: inset 0px 0px 0px $width $color; } } }

@mixin bg-cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%; }

@mixin ghostVerticalAlign {
	&:before {
		content: '';
		display: inline-block;
		vertical-align: middle;
		height: 100%;
		width: .1px; } }

@mixin centre($axis: "both") {
	@if $axis == "y" {
		position: absolute;
		top: 50%;
		-webkit-transform: translateY(-50%);
		-moz-transform: translateY(-50%);
		-ms-transform: translateY(-50%);
		-o-transform: translateY(-50%);
		transform: translateY(-50%); }
	@if $axis == "x" {
		position: absolute;
		left: 50%;
		-webkit-transform: translateX(-50%);
		-moz-transform: translateX(-50%);
		-ms-transform: translateX(-50%);
		-o-transform: translateX(-50%);
		transform: translateX(-50%); }
	@if $axis == "both" {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		-moz-transform: translate(-50%, -50%);
		-ms-transform: translate(-50%, -50%);
		-o-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%); }
	@if $axis == "none" {
		position: relative;
		top: auto;
		left: auto;
		-webkit-transform: none;
		-moz-transform: none;
		-ms-transform: none;
		-o-transform: none;
		transform: none; } }

@mixin b {
	display: block;
	position: absolute;
	content: " "; }

@mixin up {
	text-transform: uppercase;
	letter-spacing: 0.05em; }

@mixin down {
	text-transform: none;
	letter-spacing: normal; }

@mixin scaps {
	font-variant: small-caps;
	letter-spacing: 0.05em; }

@mixin clearfix {
	&:after {
		display: table;
		width: 100%;
		content: " ";
		clear: both;
		height: 0;
		width: 0; } }

@mixin opacity($level) {
	opacity: $level;
	filter: alpha(opacity=$level*100); }


@mixin text-overflow {
	overflow: hidden;
	-ms-text-overflow: ellipsis;
	text-overflow: ellipsis;
	white-space: nowrap; }

@mixin text-hide {
	font: 0/0 a;
	text-shadow: none;
	color: transparent; }

@mixin text-prevent {
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	hyphens: auto; }


@mixin font($alias, $name) {
	@font-face {
		font-family: $alias;
		src: url("../fonts/" + $name + ".eot");
		src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".ttf") format("truetype");
		font-weight: normal;
		font-style: normal; } }

@mixin font-smoothing($val: antialiased) {
	-webkit-font-smoothing: $val;
	-moz-font-smoothing: $val;
	-ms-font-smoothing: $val;
	-o-font-smoothing: $val; }


@mixin placeholder-color($color) {
	&:-moz-placeholder {
		color: $color; }
	&::-webkit-input-placeholder {
		color: $color; } }

@mixin placeholder {
	&:-ms-input-placeholder {
		@content; }
	&:-moz-placeholder {
		@content; }
	&::-moz-placeholder {
		@content; }
	&::-webkit-input-placeholder {
		@content; }
	&::placeholder {
		@content; } }

@mixin scrollbar {
	& ::-moz-scrollbar {
		@content; }
	& ::-webkit-scrollbar {
		@content; }
	& ::scrollbar {
		@content; } }

@mixin scrollbar-track {
	& ::-moz-scrollbar-track {
		@content; }
	& ::-webkit-scrollbar-track {
		@content; }
	& ::scrollbar-track {
		@content; } }

@mixin scrollbar-thumb {
	& ::-moz-scrollbar-thumb {
		@content; }
	& ::-webkit-scrollbar-thumb {
		@content; }
	& ::scrollbar-thumb {
		@content; } }

@mixin scrollbar-track-piece {
	& ::-moz-scrollbar-track-piece {
		@content; }
	& ::-webkit-scrollbar-track-piece {
		@content; }
	& ::scrollbar-track-piece {
		@content; } }

