.about-card {
	background: $color-white;
	position: relative;
	border: 1px solid $color-border-gray;
	box-shadow: 1.9px 2.3px 3px rgba(42, 42, 42, 0.07);
	@include max-screen($smartphone-landscape) {
		max-width: 225px;
		margin: 0 auto; }
	&-head {
		position: relative;
		display: block; }
	&-img-w {
		img {
			width: 100%; } }
	&-badge {
		position: absolute;
		top: -2px;
		left: 10px; }
	&-author {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		background: rgba(50, 50, 50, 0.5);
		color: $color-white;
		padding: 4px 8px;
		transition: all .3s ease-out;
		.author-thumb {
			display: flex;
			&-info {
				img {
					margin-right: 10px; } }
			&-review {
				margin-left: auto; }
			&-stars {
				color: $color-yellow--light;
				i {
					&:nth-child(1n+2) {
						margin-left: 2px; } } }
			&-num {
				margin-left: 5px; } } }
	&-body {
 }		//padding: 10px
	&-text {
		display: inline-block;
		padding: 10px;
		font-size: 1em; }
	&-foot {
		padding: 10px; }
	&-actions {
		a {
			font-size: 17px;
			transition: color .2s;
			&:hover, &:focus {
				color: $color-orange; }
			&:nth-child(1n+2) {
				margin-left: 6px; } } }
	&-price {
		margin-left: auto;
		font-size: 16px;
		font-weight: 500;
		.nums {
			font-size: 24px;
			color: $color-orange;
			font-weight: 700; } } }



