.reviews {
	.block-intro {
		.title-wrap {
			&:before {
				@include centre(y);
				left: 0;
				width: 100%;
				display: inline-block;
				content: "";
				height: 10px;
				background: $color-border-gray; } }
		.title {
			position: relative;
			display: inline-block;
			padding: 0 36px;
			margin-bottom: 0;
			background: $color-white;
			//&:before, &:after
			//include centre(y)
			//isplay: inline-block
			//ontent: ""
			//eight: 10px
			//ackground: $color-border-gray
			//&:before
			//eft: 0
			//ight: calc(50% - (-170px))
			//&:after
			//ight: 0
			//eft: calc(50% + 170px)
			.clr-red {
				vertical-align: text-top; } } }
	&-arrow {
		@include centre(y);
