.header {
	//z-index: 2
	.header-top.uk-navbar {
		color: $color-white;
		background: $color-black--light;
		padding: 15px 0;
		//.uk-flex
		//include max-screen($smartphone-portrait)
		//align-items: center
		.uk-button {
			transition: all .2s; }
		a {
			color: $color-white; } }

	.header-bot {
		background: $color-white;
		> .uk-container {
			@include max-screen($big-tablet) {
				padding: 0; } }
		.uk-navbar {
			background: $color-white;
			@include max-screen($big-tablet) {
				overflow: auto;
				-webkit-overflow-scrolling: touch; } }
		.uk-navbar-nav {
			width: 1100px;
			@include max-screen($smartphone-landscape) {
				width: 980px; }
			> li > a {
				font-size: 1.4em;
				cursor: pointer;
				height: 70px;
				line-height: 70px;
				@include max-screen($smartphone-landscape) {
					font-size: 1.2em; }
				&:hover, &:focus {
					background: transparent;
					color: $color-orange; } }
			.uk-open {
				a {
					color: $color-orange;
					background: $color-white; } }
			.uk-dropdown {
				background: $color-bg-smokie;
				border-bottom: 3px solid $color-orange;
				a {
					font-size: 1.2em;
					color: $color-black--light;
					background: transparent;
					cursor: pointer;
					&:hover, &:focus {
						color: $color-orange; } } } } }

	//&__left-nav,
	//&__right-nav
	//ont-size: 2em

	//&__right-nav
	//argin-left: 14px

	.logo {
		display: block;
		width: 176px;
		@include max-screen($big-tablet) {
			margin-left: 20px;
			max-width: 160px; }
		@include max-screen($smartphone-portrait) {
			margin-bottom: -4px;
			max-width: 120px; } }
	.search-block {
		margin-left: 60px;
		@include max-screen($big-tablet) {
			margin-left: auto; } }
	.header-ui {
		margin-left: auto;
		@include max-screen($mid-tablet) {
			display: none; }
		> * {
			&:nth-child(1n+2) {
				margin-left: 30px; } }
		.uk-icon-sort-down {
			margin-left: 6px;
			transform: translateY(-4px);
			//&:before
			//isplay: inline-block
 }			//argin-top: -4px
		.icon {
			transition: color .2s;
			&:hover, &:focus {
				color: $color-orange; } }
		.auth {
			@include max-screen($big-tablet) {
				display: none; }
			a {
				display: inline-block;
				font-size: 1.3em;
				transition: all .2s;
				&:nth-child(1n+2) {
					margin-left: 25px; }
				&:hover, &:focus {
					color: $color-yellow;
					text-decoration: none; } } } } }

.search {
	position: relative;
	.input-wrap {
		display: inline-block;
		vertical-align: middle;
		input {
			display: block;
			width: 280px; }
		&.is-open {
			opacity: 1;
			visibility: visible; }
		@include max-screen($small-tablet) {
			position: absolute;
			display: block;
			width: 285px;
			top: 0;
			right: 25px;
			opacity: 0;
			visibility: hidden;
			transition: all .2s 0.15s;
			@include max-screen($smartphone-landscape - 1) {
				width: 180px; } } }

	.uk-button {
		background: $color-orange;
		transition: all .2s;
		&:hover, &:focus {
			background: $color-orange--dark; }
		@include max-screen($small-tablet) {
			background: transparent;
			font-size: 23px;
			padding: 0;
			transition: all .4s;
			&.is-active {
				color: $color-orange!important; }
			&:hover, &:focus {
				background: inherit;
				color: inherit; } } } }
.profile {
	&__i-wrap {}
	&__info {
		margin-left: 10px; }
	&__name {}
	&__balance {
		font-size: 12px; } }
